/* =================================================================== */
/* b-graph */
/* =================================================================== */
.b-graph {
  position: relative;
  padding: 0;
  overflow: hidden;
  display: flex;
  flex: 1 1;
  background: var(--clr-back-new); }
  .b-graph__controls {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: var(--border-radius); }
    .b-graph__controls.shifted {
      width: calc(100% - 182px); }
  .b-graph.fullScreen {
    position: fixed;
    top: 0px;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    border-radius: 0; }

