  position: absolute;
  left: 50%;
  top: calc(100% + 10px);
  background: var(--clr-block);
  transition: all 0.2s;
  transform: translate(-50%, 30px);
  opacity: 0;
  visibility: hidden;
  /*border: 1px solid var(--clr-separatorD);*/
  border-radius: 5px;
  min-width: 130px;
  z-index: 10;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.45);
  /*&:before{
		content: '';
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		top: -11px;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0 8px 11px 8px;
		border-color: transparent transparent var(--clr-separatorD) transparent;
	}*/ }
  .menu-dropdown:after {
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 7px 10px 7px;
    border-color: transparent transparent var(--clr-block) transparent; }
  .menu-dropdown.open {
