    .menu-dropdown.open.to-top {
      transform: translate(-50%, 0); }
    .menu-dropdown.open.to-top-right {
      transform: translate(-85%, 0); }
  .menu-dropdown.to-right {
    left: auto;
    right: 0;
    transform: translate(0, 30px); }
    .menu-dropdown.to-right:before {
      left: auto;
      right: 30px;
      transform: translateX(0); }
    .menu-dropdown.to-right:after {
      left: auto;
      right: 31px;
      transform: translateX(0); }
  .menu-dropdown.to-top {
    top: auto;
    bottom: calc(100% + 10px);
    transform: translate(-50%, -30px); }
    .menu-dropdown.to-top:before {
      top: auto;
      bottom: -11px;
      border-width: 11px 8px 0 8px;
      border-color: var(--clr-separatorD) transparent transparent transparent; }
    .menu-dropdown.to-top:after {
